<!--><-->
<template>
  <div id="first">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="enterpriseName">
        <el-input v-model="ruleForm.enterpriseName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="enterpriseType">
        <el-input v-model="ruleForm.enterpriseType"></el-input>
      </el-form-item>
      <el-form-item label="" prop="enterpriseScale">
        <el-input v-model="ruleForm.enterpriseScale"></el-input>
      </el-form-item>
      <el-form-item label="" prop="location">
        <el-input v-model="ruleForm.location"></el-input>
      </el-form-item>
      <el-form-item label="" prop="registeredCapital">
        <el-input v-model="ruleForm.registeredCapital"></el-input>
      </el-form-item>
      <el-form-item label="" prop="registeredDate">
        <el-col :span="8">
          <el-date-picker type="date" placeholder="" v-model="ruleForm.registeredDate" style="width: 100%;"></el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="managementModel">
        <el-input v-model="ruleForm.managementModel"></el-input>
      </el-form-item>
      <el-form-item label="" prop="managementScope">
        <el-input v-model="ruleForm.managementScope" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="mainCamp">
        <el-input v-model="ruleForm.mainCamp" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="mainApplication">
        <el-input v-model="ruleForm.mainApplication" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="developing">
        <el-input v-model="ruleForm.developing" placeholder="1998"></el-input>
      </el-form-item>
      <el-form-item label="" prop="cooperativePartner">
        <el-input v-model="ruleForm.cooperativePartner" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="mainCustomer">
        <el-input v-model="ruleForm.mainCustomer"></el-input>
      </el-form-item>
      <el-form-item label="" prop="postalCode">
        <el-input v-model="ruleForm.postalCode"></el-input>
      </el-form-item>
      <el-form-item label="" prop="contactNumber">
        <el-input v-model="ruleForm.contactNumber"></el-input>
      </el-form-item>
      <el-form-item label="" prop="fax">
        <el-input v-model="ruleForm.fax" placeholder="0860-592-88887777"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="contactAddress">
        <el-input v-model="ruleForm.contactAddress"></el-input>
      </el-form-item>
      <el-form-item label="" prop="contacts">
        <el-input v-model="ruleForm.contacts"></el-input>
      </el-form-item>
      <el-form-item label="" prop="department">
        <el-input v-model="ruleForm.department"></el-input>
      </el-form-item>
      <el-form-item label="" prop="post">
        <el-input v-model="ruleForm.post"></el-input>
      </el-form-item>
      <el-form-item label="qq" prop="qq">
        <el-input v-model="ruleForm.qq"></el-input>
      </el-form-item>
      <el-form-item label="wechat" prop="wechat">
        <el-input v-model="ruleForm.wechat"></el-input>
      </el-form-item>
      <el-form-item label="" prop="authenticationDatas">
        <el-upload
          class="upload-demo"
          action=''
          multiple
          :limit="1"
          :auto-upload="false"
          :file-list="fileList_1">
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">zip2000kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="contactInfoDatas">
        <el-upload
          class="upload-demo"
          action=''
          multiple
          :limit="1"
          :auto-upload="false"
          :file-list="fileList_2">
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">zip2000kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="nextStep('ruleForm')">-</el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import option from '../../js/libs/registerOp.js'
import {insertNewMember,} from '../../api/register.js'

export default {
  name: "first",
  data() {
    return {
      ruleForm: {},
      rules: {},
      fileList_1:[],
      fileList_2:[],
    };
  },
  created(){
    this.ruleForm = option.firstForm;
    this.rules = option.firstRules;
  },
  methods: {
    nextStep(formName) {
      let that = this;
      that.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let file = {
              authenticationDatas:that.fileList_1[0],
              contactInfoDatas:that.fileList_2[1]
            }
            let data = Object.assign({},that.ruleForm,file)
            console.log(data)
        that.$refs[formName].validate(valid=>{
          if(valid){
            insertNewMember(data).then(res=>{
              if(res.result =="success"){
              that.$refs[formName].resetFields();
                that.$message({
                  showClose: true,
                  message: '',
                  type: 'success',
                  duration:2000
                });
                that.$emit('changeStep',1)
              }
              else{
                that.$message({
                  showClose: true,
                  message: res.message,
                  type: "error",
                  duration: 2000
                });
              }
            }).catch(err=>{
              that.$message({
                showClose: true,
                message: err.message,
                type: 'error',
                duration:2000
              });
            })
          }
          else{
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: '<p>,</p>',
              type: 'error',
              duration:3000
            });
          }
        })
      })
    },
    resetForm(formName) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
          this.$refs[formName].resetFields();
      })
    }
  }
}
</script>


<style lang="scss" scoped>
</style>



